<template>
    <div>
        <el-card class='styleSection'>
            <el-input v-if="inputVisible" v-model="StyleInputValue" ref="saveTagInput" size="mini" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
            </el-input>
            <el-button v-else class="buttonNewTag" size="small" @click="showInput">+ New Style</el-button>
            <template v-for='key in Object.keys(styles)'>
                <el-tag type='gray' :key='key' :closable='true' :close-transition='true' @close='handleClose(key)' :style='clickedStyles[key]' class='styleTags'>
                    <span @click='showClass(key)'>{{styles[key].name}}</span>
                </el-tag>
            </template>
        </el-card>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

var styleId = 5;
export default {
    name: 'styleSectionName',
    data() {
        return {
            inputVisible: false,
            StyleInputValue: '',
            clickedStyles: {
            }
        }
    },
    computed: mapState({
        styles: state => state.styles,
        styleClicked: state => state.textTree[state.curNodeClicked].styles,
        editStyle_clicked: state => state.editStyle_clicked,
        textTree: state => state.textTree,
    }),
    methods: {
        ...mapMutations([
            'addStyles',
            'changeEditStyle_clicked',
            'updateTextTreeStyles',
        ]),
        handleInputConfirm() {
            // StyleName
            let inputValue = this.StyleInputValue
            // ,styleskey-value.
            if (inputValue) {
                this.addStyles({ key: styleId++, value: { name: inputValue, data: '' } })
            }
            // 
            this.inputVisible = false
            this.StyleInputValue = ''
            // clickedStyles.
            this.reClickedStyles()
        },
        reClickedStyles() {
            // stylesclickedStyles
            for (let id in this.styles) {
                if (!this.clickedStyles.hasOwnProperty(id)) {
                    this.$set(this.clickedStyles, id, '')
                }
            }
        },
        showInput() {
            // . ElementUI
            this.inputVisible = true
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus()
            });
        },
        handleClose(tag) {
            // 
            //Warning:  vuexstatemutations
            // mutationcomputedstyles
            // this.deleteStyles(tag)
            this.$delete(this.styles, tag);
            // styleskey-value
            this.deleteStyleInTree(tag)
        },
        showClass(id) {
            // state
            if (id == this.editStyle_clicked) {
                // ,
                // .
                if (this.clickedStyles[id]) {
                    this.changeEditStyle_clicked('-1')
                    this.clickedStyles[id] = ''
                    return
                } else {
                    this.clickedStyles[id] = { background: '#475669', color: 'white' }
                }
            } else {
                // state
                // ididvalue
                for (let item in this.clickedStyles) {
                    if (item == id) {
                        this.clickedStyles[item] = { background: '#475669', color: 'white' }
                    } else {
                        this.clickedStyles[item] = ''
                    }
                }
            }
            this.changeEditStyle_clicked(id)
        },
        deleteStyleInTree(styleId) {
            // textTree.
            for (let item in this.textTree) {
                let index = this.textTree[item].styles.indexOf(styleId)
                if (index >= 0) {
                    this.textTree[item].styles.splice(index, 1)
                    // state. .
                    this.updateTextTreeStyles({ id: item, arr: this.textTree[item].styles })
                }
            }
        },
    },
    created() {
        // (editStyle_clicked,)
        // state
        let tempObj = {}
        for (let id in this.styles) {
            if (id === this.editStyle_clicked) {
                // 
                tempObj[id] = { background: '#475669', color: 'white' }
            } else {
                tempObj[id] = {}
            }
        }
        this.clickedStyles = tempObj
    }
}
</script>

<style scoped>
.styleSection {
    width: 100%;
    min-height: 25rem;
}

.styleSection .styleTags {
    margin: 0.2rem 0.2rem;
}

.clickedStyle {
    background: #475669;
    color: white;
}
</style>



